import React from "react";
import { motion } from "framer-motion";

export default function Project({ project, index }) {
  return (
    <motion.div className="container"
    initial={{ opacity: 0 }}
    animate={{ opacity: 1 }}
    transition={{delay: `0.${index}`}}
    >
      <img src={project.img} alt="Avatar" className="image" />
      <div className="middle">
        <div>{project.tech}</div>
      </div>
      <div className="btns">
        <a
          href={project.code}
          target="_blank"
          rel="noreferrer"
          className="text"
        >
          {project.btn1}
        </a>
        {project.btn2 ? (
          <a
            href={project.demo}
            target="_blank"
            rel="noreferrer"
            className="text"
          >
            {project.btn2}
          </a>
        ) : (
          ""
        )}
      </div>
    </motion.div>
  );
}
